<template>
    <div id="wrapper">
        <navbar id="nav" class="animate__animated animate__fadeInLeft rtl" style="display: none;"/>
        <div id="content-wrapper" style="display: none !important;" class="animate__animated animate__fadeInRight">
            <div id="content">
                <header-btn />
                <div class="container-fluid">
                    <div class="d-sm-flex justify-content-between align-items-center mb-4">
                        <h3 class="text-dark mb-0" > Working Projects </h3>
                    </div>
                    <hr>
                    <div class="row">
                        <label class="mt-2 ms-2 me-3 mb-2"> From: </label>
                        <input type="date" class="form-control col-sm-2" v-model="from" @change="getData()">
                        <label class="mt-2 ms-2 me-3 mb-2"> To: </label>
                        <input type="date" class="form-control col-sm-2" v-model="to" @change="getData()">

                        <label class=" ms-6 mt-2 me-3"> Project: </label>
                        <select v-model="work_project_id" class="form-control col-sm-2 mb-2 px-1">
                            <option value=""></option>
                            <option v-for="project in work_projects" :key="project.work_project_id" v-bind:value="project.work_project_id"> {{ project.work_project_name }} </option>
                        </select>
                    </div>
                    <table class="table table-bordered table-striped text-center mt-4" style="page-break-inside: always; font-size: 11pt !important; color: black;">
                        <thead>
                            <tr>
                                <th class="py-2">Project</th>
                                <th class="py-2">Date</th>
                                <th class="py-2">Working Employees</th>
                                <th class="py-2">Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr v-for="(item, index) in workingProjects" :key="'prrrr' + index">
                                <td>{{ item.work_project_name }}</td>
                                <td>{{ new Date(item.work_date).toISOString().split('T')[0] }}</td>
                                <td>{{ item.total_employees }}</td>
                                <td>
                                    <button class="btn btn-sm btn-success" data-toggle="modal" data-target="#mdlProjectEmployees" @click="getProjectEmployees(item.work_project_id, item.work_date)"> <v-icon color="white" size="20"> mdi-eye </v-icon> </button>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>

        <!-- Modals -->
        <change-pass/>

        <div class="modal text-dark animate__animated animate__fadeInDown animate__faster" id="mdlProjectEmployees">
            <div class="modal-dialog modal-lg">
                <div class="modal-content">
                    <div class="modal-header">
                        <button class="btn btn-danger" data-dismiss="modal">&times;</button>
                        <h5 class="mt-2"> {{ selected_work_project_name }} Employees </h5>
                    </div>
                    <div class="modal-body">
                        <table class="table table-bordered table-striped text-center " style="page-break-inside: always; font-size: 11pt !important; color: black;">
                            <thead>
                                <tr>
                                    <th class="py-2">Employee</th>
                                    <th class="py-2">Job</th>
                                    <th class="py-2">Absent</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(item, index) in projectEmployees" :key="'emp-prrrr' + index">
                                    <td>{{ item.employee_full_name }}</td>
                                    <td>{{ item.job }}</td>
                                    <td :class="`${item.absent == '0' ? 'text-success' : (item.absent == '1' ? 'text-error' : 'text-warning')}`">
                                        {{ item.absent == '0' ? 'Present' : (item.absent == '1' ? 'Absent' : 'Off') }}
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>


    </div>
</template>

<script>
import auth from '../auth';
export default {
    name: 'workingProjects',
    data() {
        return {
            from: new Date().toISOString().split('T')[0],
            to: new Date().toISOString().split('T')[0],
            workingProjects: [],
            work_projects: [],
            work_project_id: null,
            projectEmployees: [],
            selected_work_project_name: null
        }
    },
    beforeCreate() {
        auth("employees")
    },
    mounted() {
        this.$http.post('/work_projects/getData').then(r => {
            this.work_projects = r.data.filter(obj => obj.work_project_status == 'enabled')
            this.getData()
        })
    },
    
    methods: {
        getData() {
            this.$http.post('/work_projects/workingProjectsReport', {
                from: this.from,
                to: this.to,
                work_project_id: this.work_project_id
            }).then(r => {
                this.workingProjects = r.data
            })
        },
        getProjectEmployees(work_project_id, work_date) {
            this.projectEmployees = []
            this.selected_work_project_name = (this.work_projects.find(f => f.work_project_id == work_project_id) || {work_project_name: ''}).work_project_name
            this.$http.post('/work_projects/getProjectEmployees', {
                work_date,
                work_project_id
            }).then(r => {
                this.projectEmployees = r.data
            })
        }
    }

    
}
</script>